body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fonts */


/*variables */

:root {
  --header-background-image: url();
  --header-background-color: rgba(0, 0, 0, 1);
  --header-border-color: rgba(61, 61, 61, 1);
  --main-font: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  --button-background-color: rgba(155, 155, 155, 0.7); ;
  --button-background-color-hover: rgba(119, 119, 119, 0.9);;
  --button-background-color-active: rgba(109, 109, 109, 0.9);
  --navbar-color: rgb(223, 223, 223);
  --navbar-color-selected: rgb(223, 223, 223);
  --navbar-color-hover: rgb(223, 223, 223);
  --navbar-selected: rgba(146, 0, 204, 0.9);
  --navbar-hover: rgba(163, 0, 228, 0.9);
  --jumbo-color: rgba(51, 51, 51, 0.8) ;
  --jumbo-color-hover: rgba(65, 65, 65, 0.8) ;
  --range-thumb: rgb(99, 99, 99);
  --range-thumb-width: 15px ;
  --range-thumb-height: 15px ;
  --err-display: none;
  --form-control-bgr: rgba(146, 0, 204, 0.9);
  --interact-color: rgba(146, 0, 204, 0.9);

  --rechart-label-color: white;
}

/* general styling */


#align {width: 80%;
margin-left:auto; 
margin-right:auto;
font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
font-family: var(--main-font);
}

/* navbar */

.navimg {  
  position: relative;
  top: 13px;  
}

h1, h2 {
  font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  font-family: var(--main-font);

}

ul.topnav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;  
  border-bottom: 1px solid ;
  border-bottom-color: #eda338;
  font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  font-family: var(--main-font); 
}

ul.topnav li {
  float: left;   
}

ul.topnav li a {
  display: block;
  color: #FFFFFF !important;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a.selected {
  display: block;
  color: #FFFFFF !important;
  background-color: #eda338;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a:hover {
  background-color: #E7205F;
  text-decoration: none;
  color: white;
  transition: all 0.15s ease;
}

ul.topnav li.navright {
  float: right;
  background-color: #03A19A;
}

@media screen and (max-width: 560px) {
  ul.topnav li.navright, 
  ul.topnav li {float: none;}
  .navimg {display: none;}
 
}


/* transitions */

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 150ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0.01;
  transition: opacity 0ms ease-out;
}

div.transition-group {
  position: relative;

}

section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

/* cards */

.contactCards{
  width: 80%;
  margin-left:auto; 
  margin-right:auto;
}


/* general */

#flex{
  display: flex;
}

body{
  height: 100%;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.7) !important;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: linear-gradient(black, black, #322f31);
  color: white !important; 
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  text-rendering: optimizeLegibility;
	-webkit-font-feature-settings: "liga", "kern";
}

a{
  color: rgb(68, 68, 68) !important;
}

.heading{
  color: #ffffff !important;
  font-weight: 500;  
  text-shadow: -2px 1px rgba(black, 0.1);
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  
}

.card{
  background-color: rgba(145, 145, 145, 0.7) !important;
  z-index: 90;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  
}

.card:hover{
  background-color: rgba(138, 138, 138, 0.7) !important;
  transition: all 0.3s ease;
}

.jumbotron {
  background-color: rgba(51, 51, 51, 0.8) !important;
  background-color: var(--jumbo-color) !important;
  z-index: 90;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
}

.jumbotron:hover{
  background-color: rgba(65, 65, 65, 0.8) !important;
  background-color: var(--jumbo-color-hover) !important;
  transition: all 0.3s ease;
}

.form-control {
  background-color: rgba(146, 0, 204, 0.9) !important;
  background-color: var(--form-control-bgr) !important;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2) !important;
  border: 1px solid rgba(87, 87, 87, 0.9) !important;
  color: white !important;
}

.form-control-content {
  background-color: rgba(146, 146, 146, 0.9) !important;
  color: white !important;
}

.custom-control-label {
  color: white !important;
}

.custom-control-input:checked ~ .custom-control-label::before {
  background-color: rgba(80, 80, 80, 0.9) !important;
  border: 1px solid rgba(124, 124, 124, 0.5) !important;
 
}

.custom-range::-webkit-slider-thumb {
  width: 15px !important;
  width: var(--range-thumb-width) !important;
  height: 15px !important;
  height: var(--range-thumb-height) !important;
  background: rgb(99, 99, 99) !important;
  background: var(--range-thumb) !important;
  background-size: contain !important;
}

.custom-range::-moz-range-thumb {
  background-color: rgba(97, 97, 97, 0.9) !important;
  border: 1px solid rgba(255, 255, 255, 0.9) !important;
  width: 15px !important;
  width: var(--range-thumb-width) !important;
  height: 15px !important;
  height: var(--range-thumb-height) !important;
  background: rgb(99, 99, 99) !important;
  background: var(--range-thumb) !important;
  background-size: contain !important;
}

.custom-range::-moz-range-track {
  background-color: rgba(172, 172, 172, 0.9) !important;
  border: 1px solid rgba(124, 124, 124, 0.5) !important;
   }



/* navbar */

.navbar {
  /* background-color: black !important; */  
  background-color: rgba(0, 0, 0, 1);  
  background-color: var(--header-background-color);
  background-image: url();
  background-image: var(--header-background-image);
  list-style-type: none;
  border-bottom: 1px solid ;
  border-bottom-color: rgba(61, 61, 61, 1);
  border-bottom-color: var(--header-border-color);
  margin: 0 !important;
  padding: 0px !important;
  overflow: hidden;
  font-family: 'Calibri', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  font-family: var(--main-font);
  font-size: large;
  z-index: 100 !important;

}
